var reviews=[{quote:'I like this book. What is really entertaining is how the artwork is used to enhance many difficult scientific processes. This, along with the humor of the piece, will engage students and enhance their understanding of the information.',reviewer:'Pascale Pinner',award:'Presidential Award Winner for Math and Science Teaching',affiliation:'Hilo Intermediate School',location:'Hilo, HI'},{quote:'Sammy\'s Day at the Fair is not only informative but fun to read as well. The information is accurate and presented in such a way as to be relevant to all readers. In addition, the phonetic glossary is extremely well done. Bravo!',reviewer:'Natalie Rasmussen',award:'Milken National Educator Award Winner',affiliation:'North High School',location:'Minneapolis, MN'},{quote:'I have never seen a children\'s book that more clearly illustrates the complex process of digestion, and certainly not in combination with such an appealing, high-interest story. The illustrations are delightful and anatomically accurate. Everyone can learn from this book',reviewer:'Jon Roland',award:'Presidential Award Winner for Math and Science Teaching',affiliation:'Perry Hall High School',location:'Baltimore, MD'},{quote:'The story line is most creative and reinforces the science concepts and processes of digestion. I wish I had had the book when I taught exactly the same content earlier this year. It is enjoyable and easy to follow as well as to teach.',reviewer:'David O\'Shields',award:'Milken National Educator Award Winner',affiliation:'Bell Street Middle School',location:'Clinton, SC'},{quote:'I applaud the author\'s novel approach to presenting scientific information. I think students aged ten through twelve would like this method of learning about the digestive system. Instead of a fact filled textbook, they would learn the same information through a story that many can identify with.',reviewer:'Mike Barondeau',award:'Presidential Award for Excellence in Math and Science Teaching',affiliation:'Edmunds Central School',location:'Roscoe, SD'},{quote:'Sammys Day at the Fair is very well written. It is interesting and fun to read, yet factual enough to use as a classroom resource.',reviewer:'Ernie Schiller',award:'Iowa Teacher of the Year',affiliation:'Central Lee High School',location:'Donnellson, IA'},{quote:'Both children and adults will enjoy reading this story without realizing how much they are learning. I read the story many times, and each time I found myself smiling and enjoying it more. With every reading, I learned something new and chuckled at some minute detail that I had missed previously.',reviewer:'Beverly Plein',award:'Milken National Educator Award Winner',affiliation:'Cresskill Junior/Senior High School',location:'Cresskill, NJ'},{quote:'What a great book! A great job of blending humor with a very clear and understandable description of the digestive system. My ten-year-old son and daughter loved it!',reviewer:'Joan Baltezore',award:'Presidential Award for Excellence in Math and Science Teaching',affiliation:'West Fargo High School',location:'West Fargo, ND'}];

